<template>
  <gui-page customHeader="true" customFooter="true">
    <view slot="gHeader">
      <view
        style="
          display: flex;
          justify-content: space-between;
          align-items: center;
        "
      >
        <view class="BackIcon"
          ><text
            class="gui-grids-icon gui-icons"
            style="font-size: 40rpx; color: #333333; font-weight: 600"
            @click="goBack()"
            >&#xe600;</text
          ></view
        >
        <text
          class="pageTitle"
          style="
            font: inter;
            font-size: 36rpx;
            font-weight: 600;
            color: #333333;
          "
          v-if="status === '待审核'"
          >待审核详情</text
        >
        <text
          class="pageTitle"
          style="
            font: inter;
            font-size: 36rpx;
            font-weight: 600;
            color: #333333;
          "
          v-if="status === '进行中'"
          >进行中详情</text
        >
        <text
          class="pageTitle"
          style="
            font: inter;
            font-size: 36rpx;
            font-weight: 600;
            color: #333333;
          "
          v-if="status === '已完成'"
        ></text>
        <text
          class="pageTitle"
          style="
            font: inter;
            font-size: 36rpx;
            font-weight: 600;
            color: #333333;
            margin-right: 200rpx;
          "
          v-if="status === '已完成'"
          >已完成详情</text
        >
        <text
          class="pageTitle"
          style="
            font: inter;
            font-size: 36rpx;
            font-weight: 600;
            color: #333333;
          "
          v-if="status === '已拒单'"
        ></text>
        <view><text></text></view>
      </view>
    </view>
    <view slot="gBody" class="productStatusContainer">
      <view class="productDetailContainer"><ProductDetails /></view>
      <view class="serviceFeeDetailsContainer"><ServicefeeDetails /></view>
      <view class="orderInformationDetailContainer"
        ><OrderInformationDetail
      /></view>
    </view>
    <view slot="gFooter">
      <view v-if="status === '已完成'">
        <view class="completeDetail"><CompleteDetail /></view>
      </view>
      <view>
        <view class="completeDetail2"><CompleteDetail2 /></view>
      </view>
      <view v-if="status === '进行中'">
        <view class="progressButton"><ProgressButton /></view>
      </view>
      <view v-if="status === '待审核'">
        <view class="toReview"><ToReview /></view>
      </view>
      <view v-if="status === '已拒单'"> </view>
    </view>
  </gui-page>
</template>

<script>
import ProductDetails from "../../Components/ProductDetails.vue";
import ServicefeeDetails from "../../Components/ServicefeeDetails.vue";
import OrderInformationDetail from "../../Components/OrderInformationDetail.vue";
import CompleteDetail from "@/pages/ProductStatus/CompleteDetail.vue";
import CompleteDetail2 from "@/pages/ProductStatus/CompleteDetail2.vue";
import ProgressButton from "@/pages/ProductStatus/ProgressButton.vue";
import ToReview from "@/pages/ProductStatus/ToReview.vue";
export default {
  data() {
    return {
      status: "",
    };
  },
  methods: {
    goBack() {
      uni.navigateBack({
        delta: 2,
      });
    },
  },
  onLoad: function (data) {
    this.status = data.status;
  },
  components: {
    ProductDetails,
    ServicefeeDetails,
    OrderInformationDetail,
    CompleteDetail,
    CompleteDetail2,
    ProgressButton,
    ToReview,
  },
};
</script>

<style scoped>
.productStatusContainer {
  background: #f4f4f4;
  height: 100vh;
}
.serviceFeeDetailsContainer {
  margin: 15rpx 0rpx 15rpx 0rpx;
}
</style>
